@import url("https://fonts.googleapis.com/css?family=Open+Sans:400,600,700&display=swap"); @import url("https://fonts.googleapis.com/css?family=Rubik:400,500,700&display=swap"); * {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

body {
  color: #6c6c6f;
  background-color: #fff;
  font-family: 'Open Sans', sans-serif;
  font-weight: 400;
  font-size: 13px;
  line-height: 24px;
}

a {
  -webkit-transition: all 300ms ease-in-out;
  transition: all 300ms ease-in-out;
}

a:hover, a:focus {
  text-decoration: none;
  outline: 0px;
}

h1, h2, h3, h4, h5, h6 {
  color: #0f0f0f;
  font-family: 'Rubik', sans-serif;
  font-weight: 700;
}

img {
  display: block;
  width: 100%;
  height: auto;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

figure {
  margin: 0;
}

section {
  display: block;
  position: relative;
  padding: 50px 0;
  width: 100%;
}

/**
* Common Styles
*/
.avatar {
  max-width: 60px;
}

.btn, .btn-default, .btn-round, .btn-outline {
  color: #fff;
  display: inline-block;
  font-size: 14px;
  font-weight: 700;
  line-height: 0.8;
  text-transform: capitalize;
  padding: 20px 25px;
}

.btn-default {
  background-color: #5bc86d;
  border-radius: 50px;
  color: #fff;
  text-align: center;
  -webkit-transition: all 300ms ease-in-out;
  transition: all 300ms ease-in-out;
}

.btn-default:hover {
  background-color: #fff;
  -webkit-box-shadow: 0 0 30px 5px rgba(0, 0, 0, 0.05);
  box-shadow: 0 0 30px 5px rgba(0, 0, 0, 0.05);
  color: #0f0f0f;
}

.btn-round {
  border-radius: 50px;
}

.btn-outline {
  border: 2px solid #fff;
}

.btn:focus, .btn-default:focus, .btn-round:focus, .btn-outline:focus {
  -webkit-box-shadow: none;
  box-shadow: none;
}

.btn.icon-left i, .icon-left.btn-default i, .icon-left.btn-round i, .icon-left.btn-outline i {
  line-height: .8;
  margin-right: 20px;
}

.tagline {
  color: #5bc86d;
  display: block;
  font-size: 16px;
  font-weight: 600;
  margin-bottom: 5px;
}

.sec-heading {
  position: relative;
  margin-bottom: 45px;
}

.sec-title {
  font-size: 3.5em;
  color: #0f0f0f;
  font-weight: 700;
  text-transform: capitalize;
  position: relative;
}

.sec-title span {
  color: #09c;
  /* color: #5bc86d; */
  display: inline-block;
}

h2.sec-title {
  font-size: 3.5em;
  margin-bottom: 0;
}

@media screen and (max-width: 767px) {
  h2.sec-title {
      font-size: 42px;
  }
}

h3.sec-title {
  font-size: 29px;
  line-height: 40px;
  margin-bottom: 20px;
}

@media screen and (max-width: 767px) {
  h3.sec-title {
      font-size: 26px;
      line-height: 36px;
  }
}

.subtitle {
  margin-bottom: 0;
}

.content-box .sec-title, .content-box-fluid .sec-title {
  line-height: 36px;
}

.content-box .content-title, .content-box-fluid .content-title {
  font-size: 30px;
  font-weight: 400;
  line-height: 40px;
}

.content-box p, .content-box-fluid p {
  font-size: 14px;
  line-height: 36px;
}

.content-box-fluid {
  max-width: 475px;
}

.img-thumbnail {
  border: none;
  border-radius: 5px;
  background-color: #fff;
  padding: 10px;
}

.signature {
  max-width: 138px;
  width: 100%;
}

.videobox {
  -webkit-box-shadow: 0 50px 50px 0 rgba(0, 0, 0, 0.1);
  box-shadow: 0 50px 50px 0 rgba(0, 0, 0, 0.1);
  position: relative;
}

.play-btn {
  position: absolute;
  top: 50%;
  left: 0;
  right: 0;
  -webkit-transform: translateY(-50%);
  transform: translateY(-50%);
  margin: 0 auto;
  max-width: 70px;
}

.mt-reverse {
  margin-top: -140px;
}

#curve-svg {
  pointer-events: none;
  position: absolute;
  bottom: 0;
  left: 0;
  z-index: 1;
}

#curve-svg path {
  fill: #fff;
}

/**
*  Preloader
*/
#preloader {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #fff;
  z-index: 99999999;
  width: 100%;
  height: auto;
}

#status {
  width: 100px;
  height: 100px;
  position: absolute;
  left: 50%;
  top: 50%;
  /* background-image: url(../images/preloader.gif); */
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}

/**
* Top Header
*/
.top-bar {
  position: relative;
  padding: 30px 0;
}

.top-text {
  color: #0f0f0f;
  font-size: 18px;
}

@media screen and (max-width: 991px) {
  .top-text {
      font-size: 13px;
  }
}

@media screen and (max-width: 768px) {
  .top-text {
      text-align: center;
  }
}

.top-text a {
  display: block;
  color: #0f0f0f;
  font-size: 18px;
}

@media screen and (max-width: 991px) {
  .top-text a {
      font-size: 13px;
  }
}

.top-text i {
  color: #5bc86d;
  margin-right: 5px;
}

/**
* Header style
*/
.header {
  display: block;
  position: relative;
