/**
* Shopping cart Model
*/
.sc-model {
  position: fixed;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.7);
  -webkit-transition: all 400ms ease-in;
  transition: all 400ms ease-in;
  height: 100%;
  width: 100%;
  opacity: 0;
  visibility: hidden;
  z-index: 7;
}

.sc-model.active {
  opacity: 1;
  visibility: visible;
}

.sc-model.active .sc-container {
  left: 0;
}

.sc-container {
  background-color: #fff;
  padding: 20px;
  position: relative;
  left: 100%;
  -webkit-transition: all 400ms ease-in;
  transition: all 400ms ease-in;
  margin-left: auto;
  max-width: 320px;
  width: 100%;
  height: 100%;
}

.sc-container .total-amount {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
  -ms-flex-pack: justify;
  justify-content: space-between;
  -webkit-box-align: inherit;
  -ms-flex-align: inherit;
  align-items: inherit;
  color: #0f0f0f;
  font: 500 16px "Rubik", sans-serif;
  text-transform: uppercase;
  margin: 30px 0;
}

.sc-container .btn, .sc-container .btn-default, .sc-container .btn-round, .sc-container .btn-outline {
  display: block;
  border-radius: 0;
  margin-bottom: 10px;
  width: 100%;
}

.sc-container .btn:last-child, .sc-container .btn-default:last-child, .sc-container .btn-round:last-child, .sc-container .btn-outline:last-child {
  background-color: transparent;
  border: 1px solid #0f0f0f;
  color: #0f0f0f;
}

.sc-container .sc-close {
  border: 1px solid #000;
  cursor: pointer;
  color: #000;
  display: block;
  font-weight: 700;
  line-height: 30px;
  text-align: center;
  text-transform: uppercase;
  -webkit-transition: all 300ms ease-in-out;
  transition: all 300ms ease-in-out;
  position: absolute;
  right: 20px;
  height: 30px;
  width: 30px;
}

.sc-container .sc-close:hover {
  background-color: #5bc86d;
  border-color: #5bc86d;
  color: #fff;
}

.sc-item {
  border-bottom: 1px dashed #dadada;
  position: relative;
  padding: 10px 20px 10px 75px;
  min-height: 65px;
  margin-bottom: 10px;
}

.sc-item img {
  position: absolute;
  top: 0;
